body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, auto);
    color: white;
    font-size: 18pt;
    align-self: center;
    /* place-items: center; */
    top: 0;
    text-align: center;
  }
  
@font-face {
  font-family: fr-bold;
  src: url(/futurama/static/media/fr-bold.3d20efac.ttf);
}

.score {
    grid-column: 1/-1;
    grid-row: 3/4;
    font-family: fr-bold;
    font-size: 12pt;
    text-align: center;
    margin: 5px;
}
  
/* mobile */
@media (max-width: 768px) {
    .score {
font-size: 10pt;    
}
} 
@media (max-width: 768px) {
img {
    height: 100px;
    margin: 10px;
}
}   

@media (min-width: 769px) and (max-width: 1024px) {
    img {
        height: 150px;
        margin: 5px;
    }
}   

/* desktop */
@media (min-width: 1025px) {
    img {
    height: 200px;
    margin: 5px;
    }
}

.zoom {
  -webkit-transition: .2s;
  transition: .2s;
}

.zoom:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.winningPrize {
    color: white;
    font-family: fr-bold;
    height: 150px;
}

.prizesP {
    margin: 0px;
    font-size: 14pt;
}

/* mobile */
@media (max-width: 768px) {
    .prizeImg {
        height: 100px;
        margin: 10px;
    }
} 

/* ipad */
@media (min-width: 769px) and (max-width: 1024px) {
    .prizeImg {
        height: 150px;
        margin: 5px;
    }
}   

/* desktop */
@media (min-width: 1025px) {
    .prizeImg {
    height: 200px;
    margin: 5px;
    }
}

@font-face {
    font-family: fr-bold;
    src: url(/futurama/static/media/fr-bold.3d20efac.ttf);
  }

.App {
    margin: 0 auto;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 300px 1fr 255px;
    height: 100vh;
    width: 100%;
    text-align: center;
}

p {
    font-family: fr-bold;
    font-size: 16pt;
    grid-column: 1/-1;
    grid-row: 2/3;
    margin: 15px;
}

/* mobile */
@media (max-width: 768px) {
    p, .prizesP {
font-size: 10pt;    }
} 

h2 {
    font-family: fr-bold;
    grid-column: 1/-1;
    grid-row: 1/2;
    margin-bottom: 0px;
}

.header {
    grid-column: 1/-1;
    grid-row: 1/2;
}

.imageGrid {
    grid-column: 1/-1;
    grid-row: 3/4;
    width: auto;
}

/* ipad */
@media (min-width: 769px) and (max-width: 1024px) {
    .App {
        grid-template-rows: 300px 1fr 225px;
    }
}  


/* desktop */
@media (min-width: 1025px) {
    .App {
        margin: 0 auto;
        display: grid;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: 300px 1fr 225px;
    }

    .header {
        grid-column: 2/3;
        grid-row: 1/2;
    }

    .prizeDiv {
        grid-column: 2/3;
        grid-row: 2/3;
        grid-template-columns: repeat(3, auto);
        grid-template-rows: auto;
        width: 400px;
        justify-self: center;
    }

    /* .prize {
        grid-column: 2/3;
        grid-row: 1/2;
        padding-bottom: 5px;
    } */

    .imageGrid {
        grid-column: 2/3;
        grid-row: 3/4;
        width: auto;
    }

}
/* end desktop */
